@use '../../sharedfc/datepicker' as Datepicker;
@use '../../sharedfc/input' as Input;
@use './datepicker.mixins' as mixins;

@use '../../assets/scss/elements/accesibility';

@include Input.fc-input-element-vars;

@include Datepicker.fc-datepicker-item;
@include Datepicker.fc-calendar;
@include Datepicker.fc-calendar-month;
@include Datepicker.fc-calendar-year;

:host {
  @include Input.global-input;
  @include Input.fc-input-element;
  @include Input.fc-input-label;
  @include Input.fc-input-helper;
  @include Input.fc-input-feedback;

  --input-border-radius: var(--ath-border-radius-input-default);

  .ath-datepicker {
    position: relative;

    &-overlay {
      //propiedades de clase overlay en figma
      display: flex;
      padding: var(--ath-spacing-input-datepicker-calendar-overlay-padding-around);
      flex-direction: column;
      z-index: 1000;
      opacity: 0;
      transition:
        transform 300ms ease,
        opacity 300ms ease,
        visibility 300ms ease;
      visibility: hidden;

      // propiedades del padre de overlay en figma
      min-width: 288px;
      max-width: 336px;
      align-items: center;
      position: absolute;
      @include mixins.datepickerOverlayPosition('sm');
      @include mixins.datepickerOverlayPosition('md');
      @include mixins.datepickerOverlayPosition('lg');

      border-radius: var(--ath-border-radius-input-datepicker-overlay);
      border: 1px solid var(--ath-color-input-datepicker-overlay-border);
      background: var(--ath-color-bg-surface-secondary-subtlest);

      &.is-active {
        opacity: 1;
        // The value of 1.0001 fixes a Chrome glitch with scaling
        transform: scale(1.0001) translateZ(0) translateY(0);
        visibility: visible;
      }
      .ath-datepicker-calendar-date {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: var(--ath-spacing-input-datepicker-calendar-overlay-row-gap);
        align-self: stretch;

        &__header {
          display: flex;
          width: 288px;
          justify-content: center;
          align-items: center;
          gap: var(--ath-spacing-input-datepicker-calendar-header-col-gap);

          &__date {
            display: flex;
            justify-content: center;
            align-items: center;
            gap: var(--ath-spacing-input-datepicker-calendar-header-date-col-gap);
            flex: 1 0 0;
          }
        }
        &__date {
          display: contents;
        }
        &__month,
        &__year {
          display: flex;
          width: 100%;
          height: 286px;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          gap: var(--ath-spacing-input-datepicker-calendar-cell-group-row-gap);
        }
        &__footer {
          display: flex;
          justify-content: center;
          align-items: center;
          align-self: stretch;
        }
      }
    }
  }
}
